body{
    background-image: "./bg.jpeg";
  }

.landing-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.landing-container .footer {
  margin-top: 2em;
}

.login {
  width: 40em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1em auto auto auto;
  text-align: center;
  position: relative;
  z-index: 1;
}

.login .landing-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.2);
  border-radius: 4px;
  position: relative;
  z-index: 99;
  width: 100%;
  height: 100%;
  padding: 17px 10px;
}
.login .right-side {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 40%;
  background-color: #8FBC8F;
  width: 100%;
  position: absolute;
  right: -34%;
  border-radius: 6px;
  z-index: 1;
  transition: all 400ms ease-in-out;
  cursor: pointer;
  box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.281);
}
.login .right-side.right {
  right: -25%;
  align-items: flex-end;
}
.login .right-side.right:hover {
  right: -30%;
  background-color: green;
}
.login .right-side.left {
  right: 20%;
  align-items: flex-start;
}
.login .right-side.left:hover {
  right: 25%;
  background-color: green;
}
.login .right-side .text {
  font-size: 21px;
  font-weight: 500;
  color: #fff;
  margin-right: 1.5em;
  margin-left: 1.5em;
}


