img {
  width: 100%;
}

body {
  background-image: url("https://images.pexels.com/photos/62693/pexels-photo-62693.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
  background-size: cover;
}

* {
  font-family: cursive;
}

.navbar {
  background-color: #e3f2fd;
}
.fa.fa-edit {
  color: #8fbc8f;
}

.card.card-body.bg-light.delete:hover {
  cursor: pointer;
  background-color: pink !important;
}

.card.card-body.bg-light.update:hover {
  cursor: pointer;
  background-color: gainsboro !important;
}

.card.card-body.bg-light.board:hover {
  cursor: pointer;
  background-color: gainsboro !important;
}

.fa.fa-minus-circle {
  color: red;
}

.landing {
  position: relative;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::placeholder {
  color: #bbb !important;
}
